<template>
  <div>
    <!--    action="http://10.176.36.38:8443/api/scenarios/upload"-->
    <!--  ="http://localhost:8443/api/scenarios/upload"-->
    <div class="dialog-mask" v-show="myDialogVisible">
      <div class="dialog-container">
        <div class="dialog-body">
          <form class="modal">
            <span class="close" @click="handleCancel">X</span>
            <div class="content">
              <span class="title">上传图片或视频</span>
              <p class="message">
                图片请选择 jpg格式
                <br />
                视频请选择 mp4格式
              </p>
              <!--action是必要属性，上传文件的地址，可以不给，但必须要有-->
              <el-upload
                ref="upload"
                action
                :http-request="submitUpload"
                accept="application/json"
                :before-remove="deleteUploadedFile"
                multiple
                :limit="1"
                :on-exceed="handleExceed"
                :file-list="fileList"
                :before-upload="beforeUpload"
                :auto-upload="false">
                <el-button class="upload-btn" slot="trigger" style="margin-left: 100px;" size="small" type="primary">选取文件</el-button>
                <el-button class="upload-btn" style="margin-left: 10px;" size="small" type="success" @click="submit">确定</el-button>
              </el-upload>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'source-upload',
  data () {
    return {
      myDialogVisible: false,
      fileList: [],
      filePath: '',
      uploadFileName: '',
      uploadURL: ''
    }
  },
  methods: {
    handleExceed (files, fileList) {
      let message = '当前限制选择1个文件，本次选择了' + files.length + '个文件'
      if (files.length + fileList.length >= 2) {
        message = message + '，共选择了' + (files.length + fileList.length) + '个文件'
      }
      this.$message({
        type: 'warning',
        message: message
      })
    },
    deleteUploadedFile () { // 删除上传的场景文件
      this.$refs.upload.uploadFiles.pop()
      if (this.filePath !== '') {
        const index = this.filePath.lastIndexOf('/') // lastIndexOf("/")  找到最后一个  /  的位置
        const fileName = this.filePath.substring(index + 1) // substr() 截取剩余的字符，即得带后缀的文件名
        let _this = this
        this.$axios.post(this.uploadURL, {
          fileName: fileName
        }).then(res => {
          _this.filePath = ''
          _this.fileList = []
          _this.fileName = ''
        }).catch(err => {
          console.log(err)
        })
      }
    },
    clear () {
      this.$refs.upload.clearFiles()
      this.uploadFileName = ''
    },
    beforeUpload (file) {
      // 限制上传文件的类型和大小
      const isJson = (file.type === 'application/json')
      const isLimit5M = (file.size / 1024 / 1024 < 5)
      if (!isJson) {
        this.$alert('上传失败，仅支持上传json文件，请重新选择json格式的文件并上传！', '提示', {
          confirmButtonText: '确定'
        })
      }
      if (!isLimit5M) {
        this.$alert('上传失败，上传的文件大小不能超过5MB！', '提示', {
          confirmButtonText: '确定'
        })
      }
      this.uploadFileName = file.name
      return isJson && isLimit5M
    },
    submitUpload (val) {
      let fileData = new FormData()
      let _this = this
      fileData.append('file', val.file)
      this.$axios.post('/scenarios/upload', fileData)
        .then(resp => {
          if (resp && resp.data.code === 200) {
            _this.filePath = resp.data.result
            _this.$alert('上传成功，该场景文件有效，请继续选择其他测试信息', '提示', {
              confirmButtonText: '确定'
            })
            _this.uploadFileName = val.file.name
            _this.handleConfirm()
          } else {
            this.$alert(resp.data.message, '提示', {
              confirmButtonText: '确定'
            })
            _this.$refs.upload.uploadFiles.pop()
          }
        })
    },
    submit () {
      this.$refs.upload.submit()
    },
    show () {
      this.myDialogVisible = true
    },
    handleCancel () {
      this.myDialogVisible = false
      this.clear()
      this.deleteUploadedFile()
      this.$emit('resetScenarioSelect')
    },
    handleConfirm () {
      this.myDialogVisible = false
      this.$emit('uploadScenario')
    }
  }
}
</script>

<style scoped>
/*对话框的样式*/
.dialog-mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999; /*使弹窗浮现在其他元素之上*/
}

.dialog-container {
  width: 400px;
  background-color: #409EFFFF;
  border-radius: 30px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

.dialog-body {
  padding: 16px;
}

/*上传文件的组件样式*/
.modal {
  background-color: rgb(255, 255, 255);
  border-radius: 1em;
  box-shadow: 0px 100px 48px -60px #1f3f8f;
  color: rgb(15, 14, 14);
  max-width: 400px;
  overflow: hidden;
  position: relative;
  transition: background-color 0.3s, color 0.3s;
}

.content > *, .modal > * {
  padding: 0.875em;
}

.title {
  font-size: 1.25em;
  font-weight: 600;
  line-height: 1.2;
  display: flex;
  justify-content: center;
}

.message {
  line-height: 1.2;
  text-align: center;
  font-size: 20px;
}

.upload-btn {
  margin-left: 8px;
  padding: 8px 16px;
  border: none;
  background-color: #1890ff;
  color: #fff;
  border-radius: 4px;
  cursor: pointer;
  outline: none;
}

.upload-btn:hover {
  background-color: #40a9ff;
}

.close {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(226, 94, 54);
  height: 30px;
  width: 30px;
  border-radius: 50%;
  position: absolute;
  right: 10px;
  top: 10px;
  color: #fff;
  font-weight: bold;
  font-family:”Microsoft YaHei”,Arial,Helvetica,sans-serif,”宋体”;
  cursor: pointer;
}

</style>



