<template>
  <div class="login_or_signup-container">
    <div class="form-box">
      <form class="form" >
        <span class="title">登陆</span>
        <span class="subtitle">用户名和密码登陆</span>
        <div class="form-container">
          <input type="text" class="input" v-model="loginForm.username" placeholder="用户名" autocomplete="off">
          <input type="password" class="input" v-model="loginForm.password" placeholder="密码" autocomplete="off">
        </div>
        <button type="button" @click="login">登陆</button>
      </form>
      <div class="form-section">
        <p>还没有账号？ <a @click="goToSignUp">注册</a> </p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Login',
  data () {
    return {
      loginForm: {
        username: '',
        password: ''
      },
      loginURL: '/login/user'
    }
  },
  methods: {
    login () {
      let _this = this
      console.log('test')
      this.$axios
        .post(this.loginURL, {
          username: this.loginForm.username,
          password: this.loginForm.password
        })
        .then(resp => {
          if (resp && resp.data.code === 200) {
            _this.$message.success('登陆成功')
            _this.$store.commit('login', _this.loginForm)
            // var path = this.$route.query.redirect
            // this.$router.replace({path: path === '/' || path === undefined ? '/index' : path})
            this.$router.replace({path: '/'})
          } else {
            this.$message.error(resp.data.msg)
          }
        })
        .catch(failResponse => {
          console.log(failResponse)
          this.$message.warning('出错了，请稍后再试')
        })
    },
    goToSignUp () {
      this.$router.push('/signup')
    }
  }
}
</script>

<style scoped>
@import "../../assets/css/loginsignup.css";
</style>



