<template>
  <div class="login_or_signup-container">
    <div class="form-box">
      <form class="form">
        <span class="title">注册</span>
        <span class="subtitle">使用邮箱创建新账号</span>
        <div class="form-container">
          <input type="text" class="input" v-model="signupForm.username" placeholder="用户名" autocomplete="off">
          <input type="email" class="input" v-model="signupForm.email" placeholder="邮箱" autocomplete="off">
          <input type="password" class="input" v-model="signupForm.password" placeholder="密码" autocomplete="off">
        </div>
        <button type="button" @click="signUp">注册</button>
      </form>
      <div class="form-section">
        <p>已经有账号？ <a @click="goToLogIn">登陆</a> </p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'SignUp',
  data () {
    return {
      signupForm: {
        username: '',
        email: '',
        password: ''
      },
      signupURL: '/signup/user'
    }
  },
  methods: {
    signUp () {
      // let _this = this
      this.$axios
        .post(this.signupURL, this.signupForm)
        .then(resp => {
          if (resp && resp.data.code === 200) {
            this.$message({
              type: 'success',
              // center: true,
              message: '注册成功，请登陆'
            })
            this.goToLogIn()
          } else {
            this.$message.error(resp.data.msg)
          }
        })
        .catch(failResponse => {
          console.log(failResponse)
          this.$message({
            type: 'warning',
            // center: true,
            message: '出错了，请稍后再试'
          })
        })
    },
    goToLogIn () {
      this.$router.push('/login')
    }
  }
}
</script>

<style scoped>
@import "../../assets/css/loginsignup.css";
</style>



