import Vue from 'vue'
import Router from 'vue-router'
import Home from '../components/home/home.vue'
import AboutView from '../components/home/AboutView.vue'
import ModelLibraryView from '../components/model-library/ModelLibraryView.vue'
import ModelArtifactsView from '../components/model-artifacts/ModelArtifactsView.vue'
import LogIn from '../components/user/Login.vue'
import SignUp from '../components/user/SignUp.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
      redirect: '/about',
      children: [
        {
          path: '/about',
          name: 'AboutView',
          component: AboutView,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/model-library',
          name: 'ModelLibrary',
          component: ModelLibraryView,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/model-artifacts',
          name: 'ModelArtifactsView',
          component: ModelArtifactsView,
          meta: {
            requireAuth: true
          }
        }
      ]
    },
    {
      path: '/login',
      name: 'LogIn',
      component: LogIn
    },
    {
      path: '/signup',
      name: 'SignUp',
      component: SignUp
    }
  ]
})



