<template>
  <div class="search-root-container">
    <div class="search-container" style="display: flex; justify-content: center; margin-right: 15px;margin-bottom: 50px">
      <div class="search">
        <input type="text" class="search__input" ref="searchInput" v-model="keyword" placeholder="输入后，回车检索" @keyup.enter="search"/>
        <button class="search__button" @click="search">
          <svg class="search__icon" aria-hidden="true" viewBox="0 0 24 24">
            <g>
              <path d="M21.53 20.47l-3.66-3.66C19.195 15.24 20 13.214 20 11c0-4.97-4.03-9-9-9s-9 4.03-9 9 4.03 9 9 9c2.215 0 4.24-.804 5.808-2.13l3.66 3.66c.147.146.34.22.53.22s.385-.073.53-.22c.295-.293.295-.767.002-1.06zM3.5 11c0-4.135 3.365-7.5 7.5-7.5s7.5 3.365 7.5 7.5-3.365 7.5-7.5 7.5-7.5-3.365-7.5-7.5z"></path>
            </g>
          </svg>
        </button>
      </div>
    </div>
    <div class="search-result-container">
      <el-row>
        <el-col :span="6" v-for="result in searchResults" :key="result.name">
          <el-card class="result-card" shadow="hover">
            <img :src="result.cover" class="image">
            <div class="search-result-details-text">
              <span>{{result.modelName}}</span>
              <div class="bottom clearfix">
                <div class="time">{{result.intro}}</div>
                <el-button type="text" class="button" @click="showDetails(result)">查看模型</el-button>
              </div>
            </div>
          </el-card>
        </el-col>
      </el-row>
    </div>
    <loading ref="loading"></loading>
  </div>
</template>

<script>
import Loading from '../common/Loading.vue'

export default {
  name: 'Search',
  components: {Loading},
  props: {
    search_url: String,
    load_by_state_url: {
      type: String,
      default: '/models/list-by-state'
    }
  },
  data () {
    return {
      searchResults: [],
      keyword: '',
      web_a_url: 'http://10.177.35.97:4000/'
    }
  },
  mounted () {
    // document.body.style.overflow = 'hidden'
    this.loadModelsWithStateId(1)
    this.$nextTick(() => {
      this.$refs.searchInput.focus()
    })
    // this.$refs.loading.show()
  },
  methods: {
    loadModelsWithStateId (stateId) {
      let _this = this
      this.$axios.get(this.load_by_state_url + '/' + stateId)
        .then(resp => {
          if (resp && resp.data.code === 200) {
            _this.searchResults = resp.data.object
            if (_this.searchResults === null || _this.searchResults.length === 0) {
              this.handleNoResult()
            }
          } else {
            this.$message.warning(resp.data.msg)
          }
        })
        .catch(err => {
          console.log(err)
          this.$message.warning('出错了，请刷新页面')
        })
    },
    search () {
      let _this = this
      if (this.keyword === '') {
        this.$message.info('请输入搜索的关键词')
        return
      }
      this.$axios.get(this.search_url + '/' + this.keyword)
        .then(resp => {
          if (resp && resp.data.code === 200) {
            _this.searchResults = resp.data.object
            if (_this.searchResults === null || _this.searchResults.length === 0) {
              this.$message.warning('没有找到需要的信息')
              this.handleNoResult()
            }
          } else {
            this.$message.warning(resp.data.msg)
          }
        })
        .catch(err => {
          console.log(err)
          this.$message.warning('出错了，请稍后再试')
        })
    },
    handleNoResult () {
      // TODO:处理没有结果的情况
    },
    showDetails (result) {
      let _this = this
      this.$refs.loading.show()
      this.$axios.get('/models/show/' + result.id)
        .then(resp => {
          if (resp && resp.data.code === 200) {
            // 跳转到3D模型页面
            this.$message.success({
              message: '模型加载中，请稍等...',
              duration: 5000
            })
            setTimeout(() => {
              window.location.href = _this.web_a_url
            }, 5000)
          } else {
            this.$message.warning(resp.data.msg)
          }
        })
        .catch(err => {
          console.log(err)
          this.$message.warning('出错了，请稍后再试')
        })
    },
    clear () {
      this.searchResults = []
      this.keyword = ''
    }
  }
}
</script>

<style scoped>
.search-root-container {
  max-width: 100vw;
  overflow-x: hidden; /* 隐藏水平滚动条 */
  overflow-y: auto; /* 显示竖直滚动条 */
}

/*搜索框样式*/
.search {
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-align: center;
  margin-top: 4vh;
}

.search__input {
  font-family: inherit;
  font-size: inherit;
  background-color: #f4f2f2;
  /*border: none;*/
  color: #646464;
  padding: 0.7rem 1rem;
  border-radius: 30px;
  width: 18em;
  height: 3em;
  transition: all ease-in-out .5s;
  margin-right: -2rem;
  border: #646464 1px solid;
}

.search__input::placeholder {
  font-size: 1.2em;
}

.search__input:hover, .search__input:focus {
  box-shadow: 0 0 1em #00000013;
}

.search__input:focus {
  outline: none;
  background-color: #f0eeee;
}

.search__input::-webkit-input-placeholder {
  font-weight: 100;
  color: #ccc;
}

.search__input:focus + .search__button {
  background-color: #f0eeee;
}

.search__button {
  border: none;
  background-color: #f4f2f2;
  margin-top: .1em;
  padding-right: .10em;
  margin-left: -1em;
}

.search__button:hover {
  cursor: pointer;
}

.search__icon {
  height: 2.2em;
  width: 2.2em;
  fill: #b4b4b4;
}

/*搜索结果样式*/
.search-result-container {
  /*margin-top: 20px;*/
  /*width: 1200px;*/
  /*height: auto;*/
  /*margin-left: auto;*/
  /*margin-right: auto;*/
  display: flex;
  margin-top: 20px;
  /*width: 100%;*/
  overflow-x: hidden; /* 隐藏水平滚动条 */
  overflow-y: auto; /* 显示竖直滚动条 */
  max-width: 100%; /* 限制元素宽度不超过屏幕宽度 */
}

.result-card {
  margin: 5px;
  width: 19vw;
  height: 30vh;
}

.time {
  font-size: 13px;
  color: #999;
}

.bottom {
  margin-top: 13px;
  line-height: 12px;
}

.button {
  padding: 0;
  float: right;
}

.image {
  width: 100%;
  display: block;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}

.clearfix:after {
  clear: both
}

.search-result-left {
  flex: 1;
  padding: 10px;
  /*border-right: 1px solid #ccc;*/
  font-size: 15px;
  overflow: auto;
}

.search-result-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  font-size: 20px;
}

.search-result-right {
  flex: 1;
  font-size: 20px;
  margin-left: 20px;
}

.scenario-details-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 10px;
}

.scenario-details-body {
  font-size: 15px;
  margin-left: 10px;
}

.search-result-list li {
  /*border: 1px solid #e1ebfb;*/
  background-color: white;
  padding: 5px;
}

.search-result-list li:hover {
  background-color: #e1ebfb;
  cursor: pointer;
}
</style>



