<template>
  <div class="start-modeling-container">
    <el-button id="start-modeling-button" @click="startModeling">开始建模</el-button>
    <source-upload ref="upload"></source-upload>
  </div>
</template>

<script>
import SourceUpload from '../nerfstudio/source-upload.vue'
export default {
  name: 'ModelArtifactsView',
  components: {SourceUpload},
  methods: {
    startModeling () {
      this.$refs.upload.show()
    }
  }
}
</script>

<style scoped>
.start-modeling-container {
  width: 100vw;
  height: 90vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

#start-modeling-button {
  height: 40px;
}
</style>



